
import {defineComponent} from 'vue';
import {OxdText, OxdIcon} from '@ohrm/oxd';

export default defineComponent({
  name: 'OxdMainMenuItem',

  components: {
    'oxd-text': OxdText,
    'oxd-icon': OxdIcon,
  },

  props: {
    url: {
      type: String,
      required: false,
      default: '#',
    },
    active: {
      type: Boolean,
      required: false,
      default: false,
    },
    collapsed: {
      type: Boolean,
      required: false,
      default: false,
    },
    name: {
      type: String,
      required: false,
      default: '',
    },
    iconType: {
      type: String,
      required: false,
      default: null,
    },
    icon: {
      type: String,
      required: false,
      default: 'home',
    },
  },

  computed: {
    menuClasses(): object {
      return {
        'oxd-main-menu-item': true,
        active: this.active,
        toggle: this.collapsed,
      };
    },
  },
});
