
export default {
  name: 'DeadlineDate',
  props: {
    date: {
      type: String,
      required: true,
    },
    isCompleted: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    isDeadlineApproaching(date: string): boolean {
      const dateComponent = date.split(' ')[0];
      return new Date(dateComponent) < new Date();
    },
  },
};
